import { Component, h, Host, Prop } from '@stencil/core';
import { cva } from 'class-variance-authority';

const variants = cva(
    'transition[background-color,color,box-shadow] text-body hover:bg-surface-1 focus-visible:ring-active-foreground/40 relative flex h-[40px] w-full cursor-pointer items-center gap-8 truncate overflow-visible rounded-md p-8 px-16 py-8 text-left leading-[24px] font-normal duration-300 ease-in-out focus:outline-0 focus-visible:ring-2 focus-visible:ring-inset',
    {
        variants: {
            layout: {
                horizontal: 'inline-block',
                vertical: 'flex',
            },
            active: {
                true: 'z-index-20 text-active-foreground',
                false: 'text-light',
            },
        },
        compoundVariants: [
            {
                layout: 'vertical',
                active: true,
                class: 'bg-active-foreground/10',
            },
        ],
    },
);

export type Layout = 'vertical' | 'horizontal';

/**
 * @category Navigation
 * @description A tab component for the tab selector.
 */

@Component({
    tag: 'atui-tab',
    shadow: false,
})
export class AtuiTab {
    /**
     * ID of the tab
     */
    @Prop()
    tab_id: string;

    /**
     * Title to be displayed in the tab
     */
    @Prop()
    tab_title: string;

    /**
     * Styling based on the layout of the tabs
     */
    @Prop()
    layout: Layout = 'horizontal';

    /**
     * Applies styling when active
     */
    @Prop()
    is_active: boolean;

    tabEl: HTMLElement;

    handleKeyDown(event: KeyboardEvent) {
        if (event.key === 'Enter' || event.key === ' ') {
            this.tabEl.click();
        }
    }

    render() {
        const classname = variants({
            layout: this.layout,
            active: this.is_active,
        });

        return (
            <Host
                role="tab"
                aria-selected={this.is_active}
                data-active={this.is_active ? 'true' : 'false'}
                onKeyDown={(event) => this.handleKeyDown(event)}
                data-name={`tab-${this.tab_id}`}
            >
                <a
                    id={`tab-${this.tab_id}`}
                    tabindex="0"
                    class={classname}
                    ref={(el) => (this.tabEl = el)}
                >
                    {this.tab_title}
                    <slot />
                </a>
            </Host>
        );
    }
}
